import React, { useContext } from 'react';
import { AppContext } from '../App';
import { fnOutput } from '../store/function';

const CompDetail = () => {
  let { stWeather } = useContext(AppContext)
  if (!stWeather) return false

  let { icon, feelsLike, clouds, uvi, humidity, pressure, visibility } = fnOutput(stWeather.current)
  return (
    <section className='detail-section'>
      <div className="inner">
        <h2>
          <img className='icon' src={`${process.env.PUBLIC_URL}/img/icons/${icon}.gif`} alt="" />
          <span>Detail Forecast</span>
        </h2>

        <ul>
          <li>
            <h3><i className="fa-solid fa-temperature-three-quarters"></i> </h3>
            <p><b>{feelsLike}</b><sup>&#8451;</sup></p>
          </li>
          <li>
            <h3><i className="fa-brands fa-cloudversify"></i></h3>
            <p><b>{clouds}</b><small>%</small></p>
          </li>
          <li>
            <h3><i className="fa-solid fa-sun-plant-wilt"></i> </h3>
            <p>
              <b>{uvi}</b>
            </p>
            <q>
              :2 / :3~5 / :6~7 / :8~10 / :11
            </q>
          </li>
          <li>
            <h3><i className="fa-solid fa-droplet"></i> </h3>
            <p><b>{humidity}</b><small>%</small></p>
          </li>
          <li>
            <h3><i className="fa-solid fa-arrow-up-wide-short"></i> </h3>
            <p><b>{pressure}</b><small>hPa</small></p>
          </li>
          <li>
            <h3><i className="fa-solid fa-eye"></i> </h3>
            <p><b>{visibility}</b><small>km</small></p>
          </li>
        </ul>
      </div>
    </section>
  );
};

export default CompDetail;